body {
  background-color: #29323c;
}

.editBtn:hover {
  background-color: #065392 !important;
  color: white !important;
  transition: all 0.3s !important;
}

.deleteBtn:hover {
  background-color: #97180f !important;
  color: white !important;
  transition: all 0.3s !important;
}


/*=== Fonts ===*/
@font-face {
  font-family: "OpenSans";
  src: url("../public/Fonts/OpenSans-Medium.ttf");
  font-weight: medium;
}

@font-face {
  font-family: "OpenSans";
  src: url("../public/Fonts/OpenSans-Bold.ttf");
  font-weight: bold;
}

@font-face {
  font-family: "OpenSans";
  src: url("../public/Fonts/OpenSans-ExtraBold.ttf");
  font-weight: 900;
}

/*=== Fonts ===*/

.newTaskBtn:hover {
  background-color: #444c56 !important;
  transform: scale(1.01);
  transition: all 0.5s;
}

.newTaskInput {
  width: 100%;
  background-color: #999999;
  border-radius: 8px;
  color: white !important;
  margin: 10px 0 !important;
}


.newTaskInput__label {
  color: white !important;
}

.taskContainer {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.addNewTask:hover {
  transform: scale(1.05);
  transition: all 0.3s;
}
